//<INPUT (inp+tab)>====================================================================================================
input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

//</INPUT>====================================================================================================

.input-form {
  &__wrapper {
    position: relative;
  }
  &__label {
    position: absolute;
    top: 5px;
    left: 10px;
    transition: all 0.5s;
    color: #868686;
    font-size: 1rem;
  }
}

.form-input {
  width: 400px;
  display: block;
  border: none;
  border-bottom: 1px solid #8D8D8D;
  padding: 10px;
  color: $main-font-color;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  font-weight: 500;
  &::placeholder {
    color: #868686;
    font-size: 0.875rem;
  }
  &_full-width {
    width: 100%;
    margin-top: 30px;
    &::placeholder {
      font-size: 1rem;
    }
  }
  &_bordered {
    height: 50px;
    width: 200px;
    border-radius: 5px;
    border: 1px solid #CBCBCB;
  }
  &:focus + .input-form__label {
    top: -25px;
    left: 0px;
    font-size: 0.813rem;
  }
  &._filled + .input-form__label {
    top: -15px;
    left: 10px;
    font-size: 0.813rem;
  }
}

.subscribe {
  padding: 33px 0;
  background-color: #F2F3F5;
}
.subscribe-form {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @media screen and (max-width: 1279px) {
    width: 740px;
    flex-wrap: wrap;
    align-items: flex-start;
    margin: 0 auto;
  }
  @media screen and (max-width: 780px) {
    flex-direction: column;
    justify-content: flex-start;
    width: 300px;
  }
  @media screen and (max-width: 560px) {
    width: 100%;
  }
  &__text-wrapper {
    margin: 0 54px 0 0;
    @media screen and (max-width: 1279px) {
      margin: 0 0 54px 0;
    }
  }
  &__text {
    position: relative;
    font-size: 1.25rem;
    font-weight: 700;
    margin: 0;
  }
  &__inputs-wrapper {
    display: flex;
    justify-content: space-between;
    margin: 0 54px 0 0;
    @media screen and (max-width: 780px) {
      flex-direction: column;
      margin: 0 0 54px 0;
    }
    @media screen and (max-width: 560px) {
      width: 100%;
    }
  }
  &__input {
    background-color: #F2F3F5;
    &:first-child {
      margin: 0 24px 0 0;
      @media screen and (max-width: 780px) {
        margin: 0 0 24px 0;
      }
    }
    @media screen and (max-width: 1366px) {
      width: 300px;
    }
    @media screen and (max-width: 780px) {
      width: 280px;
    }
    @media screen and (max-width: 560px) {
      width: 100%;
    }
  }
}

.contact-us {
  &__input {
    width: 100%;
    margin-top: 30px;
    &::placeholder {
      font-size: 1rem;
    }
  }
}

//<SELECT (sel+tab)>====================================================================================================
.select {
  position: relative;
  line-height: 1;
  // .select__item
  &__item {
    position: relative;
  }
  // .select__title
  &__title {
    color: #000;
    cursor: pointer;
    @media (max-width: 780px) {
      font-size: 0.875rem;
    }
  }
  // .select__value
  &__value {
    display: flex;
    height: 24px;
    align-items: center;
    justify-content: space-between;
    flex-direction: row-reverse;
    span {
      height: 1em;
      overflow: hidden;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }
    &:before {
      content: "";
      display: inline-block;
      transition: all 0.3s ease 0s;
      width: 6px;
      height: 10px;
      background: url("../img/select.svg") center no-repeat;
    }
  }
  &__input {
    width: 100%;
    background-color: transparent;
    height: 100%;
  }
  // .select__options
  &__options {
    color: #000;
    display: none;
    position: absolute;
    top: 45px;
    left: -19px;
    border-radius: 0 0 4px 4px;
    width: 200px;
    background-color: #fff;
    padding: 9px 0;
    @media (max-width: 780px) {
      font-size: 0.875rem;
    }
  }
  // .select__option
  &__option {
    cursor: pointer;
    padding: 9px 19px;
    margin: 0px 0px 0px 0px;
    letter-spacing: -0.5px;
    @media (min-width: 992px) {
      &:hover {
        background: #E1E1E1;
      }
    }
  }
  &._active {
    z-index: 5;
    .select__value {
      &:before {
        transform: rotate(90deg);
      }
    }
    .select__options {
      display: block;
      box-shadow: 0px 10px 20px rgba(19, 48, 55, 0.1);
    }
  }
}
//</SELECT>====================================================================================================

.select_city-select {
  width: 200px;
  padding: 5px 16px 5px 19px;
  letter-spacing: -0.5px;
  border-radius: 17px;
  background-color: #F2F3F5;
  border: 1px solid #E1E1E1;
}

//<RADIO>=====================================================================================================

.column-list {
  margin: 43px 0 25px;
  display: flex;
  flex-direction: column;
  flex-flow: column wrap;
  height: 180px;
  @media screen and (max-width: 560px) {
    height: auto;
  }
}
.input-form__radio-wrapper {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}
.input-form__wrapper_radio {
  display: flex;
  align-items: center;
  @media screen and (max-width: 560px) {
    flex-wrap: wrap;
  }
  .input-form__radio-wrapper {
    margin-bottom: 0;
    &:first-child {
      @media screen and (max-width: 560px) {
        margin-bottom: 20px;
      }
    }
  }
}
.input-form__radio-label {
  cursor: pointer;
  width: 133px;
  line-height: 1.8;
  font-weight: 600;
  transition: all 0.4s;
  font-size: calc(0.875rem + 2 * (100vw - 320px) / (1920 - 320));
}

[type="radio"]:checked,
[type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
}
[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label
{
  position: relative;
  padding-left: 36px;
  cursor: pointer;
  display: inline-block;
}
[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 24px;
  height: 24px;
  border: 1px solid $accent-color;
  border-radius: 100%;
  background: $background-color;
  @media screen and (max-width: 560px) {
    width: 20px;
    height: 20px;
  }
}
[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
  content: '';
  width: 10px;
  height: 10px;
  background: $accent-color;
  position: absolute;
  top: 8px;
  left: 8px;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  @media screen and (max-width: 560px) {
    width: 8px;
    height: 8px;
    top: 7px;
    left: 7px;
  }
}
[type="radio"]:not(:checked) + label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}
[type="radio"]:checked + label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}

[type="radio"]:checked + label {
  color: $accent-color;
}

//</RADIO>====================================================================================================

//<Donate form>====================================================================================================
.form {
  &__section-title {
    font-weight: 600;
    font-size: calc(1.25rem + 4 * (100vw - 320px) / (1920 - 320));;
  }
  &__section-description {
    font-size: 0.875rem;
    margin-top: 23px;
    max-width: 260px;
    letter-spacing: -0.5px;
    line-height: 1.2;
    &_full-width {
      max-width: 100%;
    }
  }
}
.btn_submit-l {
  width: 58px;
  height: 58px;
  flex-shrink: 0;
  @media screen and (max-width: 560px) {
    width: 36px;
    height: 36px;
  }
}

//</Donate form>====================================================================================================

//<Get involved form>====================================================================================================

.form {
  &__tags {
    display: flex;
    flex-wrap: wrap;
    padding: 9px 0 0;
  }
  &__tag {
    font-size: 0.875rem;
    line-height: 1.2;
    padding: 12px 15px;
    border-radius: 20px;
    margin: 17px 17px 0 0;
    &.active {
      background-color: #F2F3F5;
    }
    &:hover {
      background-color: #F2F3F5;
      cursor: pointer;
    }
  }
}

//</Get involved form>====================================================================================================